export default{
	data() {
		return {
			password:''
		}
	},
	methods:{
		showPopup(){
			this.$refs.popup.open()
		},
		change(e){
			if(!e.show){
				this.password = ''
			}
		},
		close(){
			this.$refs.popup.onTap()
		},
		passwordInput(e){
			this.password = e.detail.value
		},
		complete(){
			if(this.password == ''){
				uni.showToast({title: '请输入密码', icon:'none', duration: 1500})
				return
			}
			this.$emit('complete', this.password)
			this.$refs.popup.onTap()
		}
		
	}
}
